.about-company {
	// .about-company__body
	&__body {
		overflow: hidden;
	}

	// .about-company__title
	&__title {
		max-width: 1273px;
		margin: 0 auto 69px;
		text-align: left;

		@include _screen-1440 {
			max-width: 850px;
			text-align: center;
		}

		@include _screen-690 {
			margin-bottom: 40px;
		}
	}

	// .about-company__content
	&__content {
		max-width: 1273px;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
	}

	// .about-company__row
	&__row {
		flex: 1 2 50%;
		display: flex;
		gap: 10px;
		padding: 20px 0 5rem;

		&:first-child {
			border-top: 1px solid rgb(169, 169, 169);
		}

		&:last-child {
			padding-bottom: 0;
		}

		@include _screen-690 {
			flex-direction: column;
		}

		h3 {
			@include _screen-690 {
				margin-bottom: 10px;
			}
		}

		p {
			font-size: 16px;
			font-weight: 400;
			line-height: 125%; /* 20px/16 */

			@include _320-1920 {
				font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
			}

			@include _screen-320 {
				font-size: 12px;
			}
		}

		&:not(:last-child) {
			border-bottom: 1px solid rgb(169, 169, 169);
		}
	}

	// .about-company__item
	&__item {
		flex: 0 1 50%;
	}

	// .about-company__text
	&__text {
		color: rgb(0, 0, 0);
		font-family: Averta Demo PE Cutted Demo;
		font-size: 16px;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0%;
		text-align: left;
	}
}
